import Darujme from "~/components/Darujme.vue"
import Analytics from "~/components/Analytics.vue"

export default {
  data: function() {
    return {
      supporters: [],
      loading: true,
    }
  },

  mounted: function() {
    let $this = this
    fetch ("https://projects.kohovolit.eu/api/?projectId=1200653")
    .then(function (response) {
      return response.json()
    })
    .then(function (response) {
      $this.$data.supporters = response.reverse().filter( function (item) {
        $this.loading = false
        return item.last
      })
    })
  },

  methods: {
    color: function(v) {
      if (v > 95000) {
        return "bg-success"
      } else {
        if (v > 9500) {
          return "bg-info"
        } else {
          return "bg-secondary"
        }
      }
    }
  },

  components: {
    Darujme, 
    Analytics
  }

}
